
#topBar {
  background-color: rgb(12, 12, 12);
  width: 95%;
  padding-left: 5%;
  font-size: 3vw;
  font-family: 'Bangers', cursive;
  color: white;
}

#buttons-input-wrapper {
  width: 45%;
  padding-left: 5%;
  margin-top: 1%;
  display: -webkit-flex;
  display: flex;
}

#add-button-wrapper {
  width: 30%;
}

#add-button {
  background-color: rgb(179, 110, 26);
  color: black;
  font-size: 1.5vw;
  height: 100%;
  width: 40%;
  font-family: 'Fredoka One', cursive;
  border-radius: 15px 15px;
}

#input-wrapper {
  width: 40%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

#input {
  width: 60%;
  border-radius: 15px 15px 15px 15px;
  font-size: 1vw;
}

#sort-by-wrapper {
  width: 30%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

#sort-by {
  width: 70%;
  font-size: 1.3vw;
}

select {
  outline: none;
}

textarea:focus,
input:focus {
  outline: none;
}

#no-products-message {
  color: white;
  font-size: 5vw;
  text-align: center;
  width: 45vw;
  font-family: 'Kalam', cursive;
}

#products-display-container-wrapper {
  margin-top: 1%;
  padding-left: 5%;
  display: -webkit-flex;
  display: flex;
}

#products-display-container {
  width: 45vw;
  height: 32vw;
}

#page-number-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 2vw;
  margin-top: 1.2vw;
}

#page-number {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  color: white;
  font-family: 'Fredoka One', cursive;
}

#element-wrapper {
  width: 100%;
  height: calc(24% - 1px);
  border: solid 1px white;
  display: -webkit-flex;
  display: flex;
  margin-bottom: 1%;
}

#image-wrapper {
  height: 90%;
  width: 20%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#image {
  width: 80%;
  height: 80%;
  border-radius: 15px 15px;
}

#details-wrapper {
  height: 100%;
  width: 55%;
}

#title {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 90%;
  height: 25%;
  font-size: 1.4vw;
  border: none;
  color: white;
  font-family: 'Fredoka One', cursive;
  background-color: transparent;
  resize: none;
}

#description {
  height: 70%;
  width: 95%;
  border: none;
  resize: none;
  font-size: 1vw;
  background-color: transparent;
  font-family: 'Kalam', cursive;
  color: rgb(182, 174, 174);
}

#date {
  width: 15%;
  height: 40%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-family: 'Neucha', cursive;
  font-size: 1.3vw;
  color: rgb(182, 174, 174);
}

#delete-button-wrapper {
  height: 100%;
  width: 10%;
  position: relative;
}

#price {
  width: 90%;
  height: 40%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-family: 'Neucha', cursive;
  font-size: 1.3vw;
  color: rgb(182, 174, 174);
}

#delete-button {
  position: absolute;
  bottom: 15%;
  right: 10%;
  height: 35%;
  width: 110%;
  background-color: rgb(179, 110, 26);
  font-size: 0.8vw;
  font-weight: bold;
  font-family: 'Fredoka One', cursive;
  border-radius: 15px 15px;
}

#prducts-display-wrapper {
  width: 43%;
  height: 32vw;
  border: solid 2px black;
  margin-left: 2%;
  position: relative;
  background-color: rgb(94, 91, 91);
  border-radius: 15px 15px;
}

#prducts-display-sub-wrapper {
  width: 90%;
  height: 95%;
  position: absolute;
  left: 5%;
  top: 1%;
}

#display-image-wrapper {
  display: -webkit-flex;
  display: flex;
  height: 20%;
  width: 20%;
  margin-top: 0.5vw;
}

#display-image {
  width: 100%;
  height: 100%;
  border: solid 2px rgb(12, 12, 12);
  border-radius: 15px 15px;
}

.display-title {
  font-size: 1.3vw;
  margin-top: 0.8vw;
  height: 1.6vw;
  resize: none;
  font-family: 'Fredoka One', cursive;
}

#product-name {
  border: solid 2px black;
  height: 7%;
  width: 99%;
  font-size: 1.2vw;
}

#product-description {
  border: solid 2px black;
  height: 30%;
  width: 99%;
  font-size: 1.2vw;
  resize: none;
}

#bottom-wrapper {
  display: -webkit-flex;
  display: flex;
  height: 7%;
  width: 100%;
}

#product-price {
  border: solid 2px black;
  height: 100%;
  width: 20%;
}

#error-wrapper {
  width: 60%;
}

#error-message {
  font-size: 1.2vw;
  height: 100%;
  font-family: 'Bangers', cursive;
  display: -webkit-flex;
  display: flex;
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-left: 0.2vw;
  margin-right: 0.2vw;
}

#save-button {
  width: 20%;
  height: 100%;
  background-color: rgb(179, 110, 26);
  border-radius: 15px 15px;
  border-color: black;
  font-weight: bolder;
  font-size: 1.5vw;
}

body {
  margin: 0;
  padding: 0;
  background-color: rgb(12, 12, 12);
}

